

























































footer {
  @apply flex;

  > .container {
    @apply flex items-center;
  }

  a {
    @apply text-white no-underline font-bold text-xs mr-3;
  }

  i {
    @apply text-yellow mr-3 inline-block relative;
    font-size: 8px;
    top: -1px;
    transform: rotate(-90deg);
  }
}
